.header__burger-button {
  position: relative;
  padding: 0;
  margin-right: 30px;
  width: 24px;
  height: 24px;
  cursor: pointer;
}

.header__burger-button:hover {
  opacity: 0.8;
}

.header__burger-button:before {
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
  height: 3px;
  background-color: white;
  transition: all 0.6s ease;
}

.header__burger-button span {
  content: "";
  position: absolute;
  top: 10.5px;
  width: 100%;
  height: 3px;
  background-color: white;
  transition: all 0.6s ease;
}

.header__burger-button:after {
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 3px;
  background-color: white;
  transition: all 0.6s ease;
}
